@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;500&family=Oswald:wght@500&family=Playfair+Display:wght@600;700&family=Poppins:wght@300;400;500;600&display=swap');
@media (max-width:85em){
    .container{
        width: 116rem;
        padding:0 2rem ;
    }
    h1{
        font-size: 4.2rem;
        line-height: 1.3;
        font-weight: 800;
    }
    .header-text{
        font-size: 1.6rem;
        line-height: 1.5;
    }
    .btn:link,.btn:visited {
        font-size: 1.2rem;
        font-weight: 500;
        padding: 0 -0.5rem ;
    }
    .image-1{
        width: 85%;
    }
    h2{
        font-size: 3rem;
    }
    strong{
        font-size: 1.7rem;
        font-weight: 600;
    }
    .features-text{
        font-size: 1.45rem;
        line-height: 1.7;
    }
    .testimonial-text{
        font-size: 1.4rem;
        line-height: 1.6;
    }
    .testimonial-author{
        font-size: 0.9rem;
    }
    .grid-3-cols{
        column-gap: 3rem;
    }
    h3{
        font-size: 1.8rem;
    }
    .chair-details li{
        font-size: 1.2rem;
    }
    .btn--big{
        margin-top: 2rem;
    }
    .form-contain {
        margin-left: 5rem;
      }
      .f-info{
        font-size:1.4rem ;
      }
      .remain-list{
        font-size: 1rem;
      }
      .grid-footer{
        width: 80%;
        justify-content: center;
      }
      .conatiner-media{
        max-width:110rem;
      }
      .heading-22{
        font-size: 3.4rem;
      }
      .call-text{
        font-size: 1.4rem;
        line-height: 1.6;
      }
      form label{
        font-size: 1.1rem;
      }
      form input, select{
        font-size: 1.2rem;
      }
      .btn.btn--small:link{
        width: auto;
        padding: 0.4rem 0.5rem;
        font-size: 1rem;
        
      }
      .chair{
        width: 77%;
      }
     form button{
        font-size: 1.2rem;
        padding: 1rem 1rem !important;
      }

}
@media(max-width:74em){
    html{
        font-size: 57.25%;
        /* background-color: black; */
    }
    .grid-2{
        gap: 5rem;
        height: auto;
    }
    form{
        row-gap: 1rem;
        width: 85%;
    }
    .chair:nth-of-type(1){
        justify-self: end;
    }
    .chair:nth-of-type(2){
        justify-self: center;
    }
    .chair:nth-of-type(3){
        justify-self: start;
    }
    .nav-img.nav-img-2{
        height:1rem;
        width: 2rem;
    }
    .container:last-child{
       width: 100rem;
    }
    .grid-footer{
        gap: 3rem;
    }
    .copyright{
        font-size: 0.8rem;
    }
    .nav-img-2{
        overflow: hidden;
        justify-items: center;
        align-items: center;
        justify-content: center;
    }
}
@media(max-width:65.6em){
    .container{
        padding: 0 4rem;
    }
    .container{
        width: 107rem;
    }
    /* #media-testimonial{
        width: 100rem;
    } */
    h1{
        font-size: 3.2rem;
        line-height: 1.3;
        font-weight: 800;
    }
    .header-text{
        font-size: 1.3rem;
        line-height: 1.5;
    }
    .btn:link,.btn:visited {
        font-size: 1.2rem;
        font-weight: 500;
        padding: 0 -0.5rem ;
    }
    .image-1{
        max-width: 100%;
    }
    h2{
        font-size: 2.7rem;
    }
    strong{
        align-self: center;
        
        font-size: 1.6rem;
        font-weight: 800;
    }
    .features-text{
        font-size: 1.35rem;
        line-height: 1.7;
    }
    .testimonial-text{
        font-size: 1.3rem;
        line-height: 1.6;
    }
    .testimonial-box h2{
        font-size: 2.5rem;
    }
    .chair-price{
        gap: 1rem;
        flex-direction: column;
    }
    .btn{
        align-self: center;
    }
}
@media(max-width:61.3em){
    .container{
        max-width: 95rem;
    }
    .btn:link,.btn:visited {
        font-size: 1rem;
        font-weight: 500;
        padding: 0 -0.5rem ;
    }
    header{
        column-gap: 4rem;
    }
    .grid-2{
        gap: 4rem;
        height: auto;
    }
    form{
        row-gap: 1rem;
        width: 100%;
        margin-bottom: 2rem;
    }
    .chair:nth-of-type(1){
        justify-self: end;
    }
    .chair:nth-of-type(2){
        justify-self: center;
    }
    .chair:nth-of-type(3){
        justify-self: start;
    }
    .nav-img.nav-img-2{
        height:1rem;
        width: 2rem;
    }
    /* .container:last-child{
       width: 100rem;
    } */
    .grid-footer{
        gap: 3rem;
    }
    .copyright{
        font-size: 0.6rem;
    }
    .nav-img-2{
        overflow: hidden;
        justify-items: center;
        align-items: center;
        justify-content: center;
    }
    h2{
        font-size: 2.4rem;
    }
    .w--6{
        margin-bottom: 1rem;
    }
    strong{
        font-size: 1.4rem;
        font-weight: 800;
    }
    .features-text{
        font-size: 1.2rem;
        line-height: 1.7;
    }
    .testimonial-section{
        margin: 2.5rem 0;
    }
    .testimonial-text{
        font-size: 1.1rem;
        line-height: 1.6;
    }
    .testimonial-box h2{
        font-size: 2rem;
    }
    section{
        margin-top: 6rem;
    }
    button.btn{
        width: 100%;
        padding: 1rem 0;
    }
    .testimonial-section .grid-3-cols {
        grid-template-columns: 1fr 2fr;
    }
    .chair-details li{
        padding-top: 1rem;
    }
}
@media(max-width:54em){
    html{
        font-size: 49%;
    }
    .heading-22{
        font-size:2.5rem ;
        padding-bottom: 0;
    }
    .call-text{
        font-size: 1.3rem;
    }
    form label{
        font-size: 1rem;
        margin-left: 0.5rem;
      }
      form input, select{
        font-size: 1.1rem;
        letter-spacing: 0.2px;
      }
      h3{
        font-size: 1.5rem;
      }
      .chair-details li{
        font-size: 1rem;
      }
}
@media(max-width:54em){
    html{
        font-size: 45%;
    }
   
    h1{
        font-size: 3.5rem;
    }
    header{
        justify-items: center;
        grid-template-columns: 1fr;
    }
    .header-text-box{
        justify-self: center;
        width: 60%;
    }
    
    .image-1{
        width: 55%;
        height: 90%;
    }
    .btn:link,.btn:visited {
        display: inline-block;
       margin: 0 0rem 3rem ;
        font-weight: 500;
        
    }
    .grid-3-cols{
        grid-template-columns: 1fr 1fr;
    }
    .chair:nth-of-type(1){
        justify-self: center;
    }
    .chair:nth-of-type(2){
        justify-self: start;
    }
    .chair:nth-of-type(3){
        margin-top: 6rem;
        justify-self: center;
    }
.abouts div{
    text-align: center;
    width: 60%;
    justify-self: center;
}
.abouts div:first-child{
    margin-bottom: 3rem;
}
.feature-text{
    width: 50%;
}
form{
    width: 50%;
    
    grid-template-columns: 1fr;
}
.nav-display .nav-pos{
    padding-top: 5rem;
    position: absolute;
    background-color: rgb(10, 10, 10,0.9);
    top: 0;
    left: 0;
    width: 100%;
    height: 40%;
}
.nav-list span{
    color: white;
    font-size: 2rem

}
.nav-no{
    display:none;
}
.nav-display .nav-no{
    display: inline-block;
}
.nav-display .nav-list{
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.nav-hide{
    display: inline-block;
}
 #icon[name="menu-outline"]{
   
    display: inline-block;

}
.nav-display #icons{
    display: inline-block;
}
.nav-display #icon{
    display: none;
}
nav.nav-display{
    margin-bottom: 20rem;
    transition: all 0.3s ease-in;

}
.chair-details li{
    font-size: 1.1rem;
    margin-bottom: 0.5rem;
}
}