@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;500&family=Oswald:wght@500&family=Playfair+Display:wght@600;700&family=Poppins:wght@300;400;500;600&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size: 62.5%;
}
*:focus {
  outline: none;
  box-shadow: inset 0px 0px 0px 3px rgb(0, 0, 0, 0.2);
}
body {
  overflow-x: hidden;
  font-family: "Inter", sans-serif;
  color: #555;
}

.container {
  width: 120rem;
  margin: 0 auto;
 
  /* border-bottom: 8px solid #087f5b; */
}
.nav-1 {
  /* position: fixed; */
  /* width: 100vw; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
  background-color: #f9dcc4;
  /* max-height: 7rem; */
  /* position: sticky; */
}
.nav-img {
  
  width: 15rem;
  height:5rem;
}
.nav-img:hover {
  filter: saturate(3) invert();
}
/* .nav-items {
  padding: 1.5rem 0 ;
} */
.nav-list {
  display: flex;
  margin-right: 2rem;
  padding: 1rem 0;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  list-style-type: none;
  align-items: center;
  justify-content: flex-start;
  gap: 1.4rem;
}
.nav-list a:visited,
.nav-list a:link {
  font-family: "Poppins", sans-serif;
  display: inline-block;
  text-decoration: none;
  color: #161a1d;
  font-size: 1.5rem;
  font-weight: 500;
  /* border: 1px solid #fb8500; */
  padding: 0.6rem 1rem;
  border-radius: 0.7rem;
  transition: transform 0.2s ease-in-out;
}
.nav-list a:hover {
  transform: translateY(-1px);
  color: #fb8500;
}
.nav-list li:last-child a {
  background-color: #fb6107;
  color: white;
  /* margin-right: 1rem; */
  
}
header,
section {
  margin-bottom: 4.8rem;
}

h2 {
  margin-bottom: 5rem;
  font-size: 3.6rem;
 
}

.grid-3-cols {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 5rem;
}

header {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 8rem;
  margin-top: 6.4rem;
}

.header-text-box {
  align-self: center;
}

h1 {
  margin-bottom: 2.4rem;
  line-height: 1.3;
  letter-spacing: -0.08rem;
  font-size: 5rem;
  color: #114240;
}

.header-text {
  margin-bottom: 2.4rem;
  font-size: 1.8rem;
  line-height: 1.4;
  color: #777;
}

img {
  width: 100%;
}

.features-icon {
  padding: 0;
}

.features-title {
  margin-bottom: 1.6rem;
  font-size: 2rem;
}

.features-text {
  font-size: 1.7rem;
  line-height: 1.3;
}


.testimonial-section {
  margin-top: 7rem;
  background-color: #7a542e;

  padding: 2rem;
  color: white;
  /* padding: 0; */
  font: optional;
  border-radius: 1rem;
}

.testimonial-box {
  grid-column: 2 / -1;
  align-self: center;
}

.testimonial-box h2 {
  margin-bottom: 2.4rem;
  font-size: 3rem;
}

.testimonial-text {
  font-style: italic;
  margin-bottom: 2.4rem;
  font-size: 1.7rem;
  line-height: 1.5;
  background-clip: text;
}
.testimonial-author {
  font-size: 1.2rem;
  color: rgb(255, 255, 255, 0.8);
}

.chair{
  width: 90%;
}
.chair-box {
  padding: 2.4rem;
}

h3 {
  margin-bottom: 2.4rem;
  font-size: 2.2rem;
  margin-top: 2rem;
}

.chair-details {
  list-style: none;
  margin-bottom: 5rem;
}

.chair-details li {
  display: flex;
  align-items: center;
  font-size: 1.3rem;
  gap: 1rem;
  padding-top: 1.6rem;
  margin-bottom: 1.4rem;
}

.chair-details li:last-child {
  margin-bottom: 0;
}

.chair-icon {
  padding: 0;
}

.chair-price {
  font-size: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

footer {
  margin-top: 3rem;
 
  font-size: 1.4rem;
  letter-spacing: -0.03rem;
}

.btn:link,
btn:visited {
  
  padding: 0.5rem 1.2rem 1rem 1rem;
  text-decoration: none;
  border-radius: 1.3em;
  display: inline-block;
  font-weight: 500;
  font-size: 1.5rem;
  text-transform: uppercase;
  transition: background-color 0.5s ease-in-out;
}
.btn:hover,
btn:active {
  background-color: rgb(220, 245, 224, 0.7);

  color: inherit;
}
.btn--small:link {
  background-color: #087f5bcf;
  color: white;
  display: inline-block;
  font-size: 1.4rem ;
  padding: 0.6rem 0.8rem;
  border-radius: 1em;
 
}
.btn--small:hover {
  background-color: #3f784c;
  color: white;
}
.btn--big {
  
  width: 30% !important;
  text-align: center;
  color: white;
  background-color: #087f5b;
}

.w-6 {
  width: 1.2em;
  height: 1.4em;
}
.w-6:hover {
  color: orangered;
}
.w--6 {
  color: #087f5b;
  width: 2.4em;
  height: auto;
  margin-bottom: 2.4rem;
 
}
.icons {
  stroke: #7a542e;
  width: 2.4rem;
  align-self: center;
  height: 2rem;
}
figure {
  box-shadow: 0px 8px 10px 5px rgb(0, 0, 0, 0.1),
    -1px -2px 10px -4px rgb(0, 0, 0, 0.2);
  border-radius: 2rem;
  transition: all 0.3s ease-out;
}

figure img {
  border-radius: 2rem 2rem 0px 0px;
}
.image-1 {
  border-radius: 1rem;
  box-shadow: 1px 3px 10px -8px rgb(0, 0, 0, 0.9);
  height: auto;
}
section {
  margin-top: 10rem;
}

/* Footer */
footer {
  padding-bottom: 3rem;
  background-color: #172121;
}
.grid-footer {
  
  font-family: "Inter", sans-serif;

  display: grid;
  grid-template-columns: 1.5fr 1.5fr 1fr;

  gap: 8rem;
}

footer {
  /* margin: 0 2rem; */
  font-size: 1rem;
  padding-top: 4rem;
}
.nav-img-2 {
  border-radius: 2rem;
  height: 10rem !important;
  width: 20rem !important;
  filter: invert();
  /* margin-bottom: 2.2rem; */
}
.copyright {
  font-family: inherit;
  font-size: 1.3rem;
  font-weight: normal;
  /* margin-top: 3rem; */
  line-height: 1.6;
  color: #348aa7;
  margin-top: auto;
}
.grid-footer ul > li,
.grid-footer a:link {
  color: inherit;
  text-decoration: none;
  list-style: none;
}
.f-info {
  color: #fac05e;
  letter-spacing: 1px;
  margin-bottom: 3rem;
  font-size: 1.8rem;
  font-weight: 600;
}
.remain-list {
  font-weight: 400;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  font-size: 1.3rem;
}
.remain-list > li > a {
  letter-spacing: 0.1px;
  color: #fff4e4 !important;
}
#logos {
  display: flex;
  gap: 2.4rem;
}
.footer-icon {
  font-size: 3rem;
}
.i1 {
  color: blue;
  filter: contrast(2);
}
.i2 {
  color: #3f88c5;
  filter: contrast(2);
}
.i3 {
  color: #eb4b98;
  filter: contrast(2);
}
.mea-icon {
  font-size: 2rem;
  color: #e7e247;
}
.grid-1 {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
address {
  font-size: 1.4rem;
  font-style: normal;
  color: #555;
  line-height: 1.6;
}
.adress {
  display: flex;
  gap: 0.3rem;
  /* justify-content: center; */
  margin-left: 1rem;
  padding-bottom: 1.8rem;
}
.phone a {
  color: #efe7da !important;
}
.phone {
  margin-left: 1.1rem;
}
.add {
  color: #efe7da;
}
.mailing {
  color: #348aa7;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  letter-spacing: 0.6px;
}
#align-left {
  text-align: left !important;
}
.heading-fetaure {
  margin-bottom: 10rem;
}
.shadow-2 {
  box-shadow: 1px 1rem 1.4rem rgb(0, 0, 0, 0.2);
}
.grid-2 {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-auto-rows: auto;
  gap: 8rem;
  height: 55rem;
  margin-bottom: 10rem;
  background-color: #218380;
  border-radius: 11px;
  overflow: hidden;
}

.image-right img {
  width: 100%;
  height: 100%;
  /* background-size: cover; */
  background: linear-gradient(
      to right bottom,
      rgb(249, 220, 196, 0.25) 0 50%,
      rgb(249, 220, 196, 0.25) 50% 100%
    )
}
.image-right:hover {
  filter: contrast(1) saturate(1.2);
  
  
}
.sign-up {
  padding: 0rem 1.8rem;
}
.m-top {
  margin-top: 12rem;
}
.heading-22 {
  padding: 3rem 0 2rem 0;
  font-size: 4rem;
  color: #d3e6e6;
  font-family: "Poppins", sans-serif;
  font-weight: 600 !important;
  margin-bottom: 1rem !important;
  /* letter-spacing: 1px; */
}
.call-text {
  font-size: 1.8rem;
  line-height: 1.7;
  color: #90c1c0;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  margin-bottom: 4rem;
}
.form-contain {
  margin-left: 10rem;
}
.input {
  border: none;
}
form {
  font-family: "Inter", sans-serif;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 3rem;
  row-gap: 3rem;
}
form label {
  display: block;
  letter-spacing: 0.7px;
  font-size: 1.4rem;
  font-weight: 500;
  padding-right: 0.3rem;
  color: #fffff2;
  margin-bottom: 1rem;
}
form button{
  margin-top: 2.5rem !important;
}
form input,
select {
  border: none;
  font-family: inherit;
  border-radius: 10px;
  width: 100%;
  padding: 1.2rem;
  font-size: 1.5rem;
  /* color: #555; */
  color: #e1dbc7 !important;
  background-color: #524948aa;
}
input::placeholder,
select {
  color: inherit;
}
.btn {
  align-self: end;
  padding: 1rem 0 1rem 0.5rem;
  display: inline-block;
  width: 50%;
  padding: 1rem 1rem;
  border-radius: 1rem;
  border: none;
  color: white;
  background-color: #0a0908;
  font-weight: 600;
  cursor: pointer;
  text-align: center;
  transition: transform 0.2s ease-out;
}
.btn:hover {
  transform: translate(0, -1px);
}
.nav-hide{
  display: none;
}
#icon,#icons{
  color: white;
  font-size: 3rem;
  z-index: 100;
  display: none;
}